* {
  margin: 0;
  padding: 0;
}

.menu {
  background: rgba(29, 161, 242, 1);
  padding: 30px 0px;
  box-shadow: 0 5px 8px -2px rgb(0 0 0 / 20%);
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  top: 0;
  margin-bottom: 20px;
}

.menu select {
  width: 200px;
  padding: 10px;
  border-radius: 6px;
  border: 0;
  font-size: 25px;
  font-weight: bold;
  outline: 0;
  cursor: pointer;
}

label {
  font-family: sans-serif;
  font-weight: bold;
  font-size: 25px;
  padding: 20px;
  width: 200px;
  align-items: center;
  text-align: center;
  color: white;
  cursor: pointer;
}

label:hover {
  border-left: 2px solid whitesmoke;
  border-right: 2px solid whitesmoke;
  color:whitesmoke;
}

.active {
  color:#1a4e4e;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container-tweets {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.twitter-tweet.twitter-tweet-rendered {
  width: 500px !important;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid rgba(29, 161, 242, 0.7);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin: 300px auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}